
/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */
'use client'
import { useParams } from "react-router-dom";
import { useEffect, useState, useCallback, useMemo } from "react";
import { Button, Label, TextInput } from "flowbite-react";
import { Loader2 } from 'lucide-react';
import { toast } from "react-toastify";
import PageTitle from "../Helpers/PageTitle";
import Layout from "../Partials/Layout";
import commandeService from "../../services/CommandeService";
import formatDate from "../../utils/date-format";
import formatPrice from "../../utils/formatPrice";
import PaiementService from "../../services/paimentService";
import SEOHeader from "../Partials/Headers/HeaderOne/SEOHeader";
import { useAuthContext } from "../../contexts/useAuthContext";
import WaveOrangePaymentService from "../../services/Payment/wave-orange-payment-service";

export default function OrderPage() {
  const { id } = useParams();
  const { user } = useAuthContext();
  const [commande, setCommande] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [showPaymentModal, setShowPaymentModal] = useState(false);
  const [montantAPayer, setMontantAPayer] = useState(0);
  const [errorMontantAPayer, setErrorMontantAPayer] = useState("");
  const [stateButton, setStateButton] = useState(true);

  // Récupération des données de la commande
  const fetchModels = useCallback(async () => {
    try {
      setIsLoading(true);
      const data = await commandeService.getCommandeById(user.id, id);
      setCommande(data);
    } catch (error) {
      console.error("Erreur lors de la récupération des données de la commande", error);
      toast.error("Erreur lors de la récupération des données de la commande");
    } finally {
      setIsLoading(false);
    }
  }, [user.id, id]);

  useEffect(() => {
    fetchModels();
  }, [fetchModels]);

  // Détection du type de commande
  const isTypeOrder = useMemo(() => {
    return String((commande && commande.type_sale) || "").toLowerCase() === "order";
  }, [commande && commande.type_sale]);

  // Logique des lignes de paiement
  const paymentLines = useMemo(() => {
    return Array.isArray(commande && commande.payment_lines) ? commande.payment_lines : null;
  }, [commande]);

  const acompteLine = useMemo(() => {
    if (!paymentLines) return null;
    return paymentLines.find(pl => pl.sequence === 1) || paymentLines[0] || null;
  }, [paymentLines]);

  const acompteDue = useMemo(() => {
    if (!acompteLine) return false;
    return !acompteLine.state;
  }, [acompteLine]);

  // Calcul du montant minimum à payer
  const minAmountToPay = useMemo(() => {
    const residual = Number((commande && commande.amount_residual) || 0);
    if (isTypeOrder) {
      return residual > 0 ? 50 : 0;
    }
    if ((commande && commande.payment_mode) === "echelonne") {
      if (acompteDue && acompteLine) {
        return Math.max(0, Number(acompteLine.amount) || 0);
      }
      if (residual <= 0) return 0;
      return Math.min(Math.max(1000, 0), residual);
    }
    return 1000;
  }, [commande && commande.amount_residual, commande && commande.payment_mode, isTypeOrder, acompteDue, acompteLine]);

  // Pré-remplir le champ montant à payer
  useEffect(() => {
    if (!commande) return;
    if (isTypeOrder) {
      setMontantAPayer(minAmountToPay);
      setErrorMontantAPayer("");
      return;
    }
    if (commande.payment_mode === "echelonne") {
      setMontantAPayer(minAmountToPay);
      setErrorMontantAPayer("");
    } else if (commande.payment_mode === "online") {
      setMontantAPayer(Number(commande.amount_total || 0));
      setErrorMontantAPayer("");
    } else {
      setMontantAPayer(0);
      setErrorMontantAPayer("");
    }
  }, [commande, minAmountToPay, isTypeOrder]);

  // Validation du paiement
  const validerPaiment = (e, amount) => {
    e.preventDefault();
    const residual = Number((commande && commande.amount_residual) || 0);
    const value = Number(amount || 0);

    if (isTypeOrder) {
      if (residual <= 0) {
        toast.info("Aucun montant restant à payer.");
        return;
      }
      if (value <= 0) {
        setErrorMontantAPayer("Le montant doit être > 0.");
        return;
      }
      if (value > residual) {
        setErrorMontantAPayer("Le montant ne doit pas dépasser le montant restant à payer.");
        return;
      }
      setErrorMontantAPayer("");
      setShowPaymentModal(true);
      return;
    }

    if (commande.payment_mode === "echelonne") {
      if (residual <= 0) {
        toast.info("Aucun montant restant à payer.");
        return;
      }
      if (acompteDue) {
        if (value !== Number(minAmountToPay)) {
          setErrorMontantAPayer(`Le montant doit être exactement l'acompte requis (${formatPrice(minAmountToPay)}).`);
          return;
        }
      } else {
        if (value < Number(minAmountToPay)) {
          setErrorMontantAPayer(`Le montant doit être ≥ ${formatPrice(minAmountToPay)}.`);
          return;
        }
      }
      if (value > residual) {
        setErrorMontantAPayer("Le montant à payer ne doit pas dépasser le montant restant à payer");
        return;
      }
      setErrorMontantAPayer("");
      setShowPaymentModal(true);
      return;
    }

    if (commande.payment_mode === "online") {
      if (value < 1000) {
        setErrorMontantAPayer("Le montant minimum est de 1000 FCFA");
        return;
      }
      if (value !== Number(commande.amount_total || 0)) {
        setErrorMontantAPayer("Le montant à payer doit correspondre au total de la commande");
        return;
      }
      setErrorMontantAPayer("");
      setShowPaymentModal(true);
      return;
    }

    if (commande.payment_mode === "domicile") {
      toast.info("Le paiement se fera à domicile.");
      return;
    }
  };

  // Gestion du paiement
  const handlePay = async (paymentData) => {
    setIsLoading(true);
    try {
      await PaiementService.createCommandePaiment(commande.id);
      toast.success("Paiement validé avec succès");
      window.location.reload();
    } catch (error) {
      console.error("Erreur lors de la création du paiement :", error);
      toast.error("Erreur lors de la création du paiement");
    } finally {
      setIsLoading(false);
    }
  };

  // Gestion de l'état du bouton
  useEffect(() => {
    if (!commande) return;
    const residual = Number((commande && commande.amount_residual) || 0);
    const value = Number(montantAPayer || 0);

    if (isTypeOrder) {
      if (residual <= 0) {
        setStateButton(true);
        return;
      }
      if (value <= 0) {
        setErrorMontantAPayer("Le montant doit être > 0.");
        setStateButton(true);
        return;
      }
      if (value > residual) {
        setErrorMontantAPayer("Le montant ne doit pas dépasser le montant restant à payer.");
        setStateButton(true);
        return;
      }
      setErrorMontantAPayer("");
      setStateButton(false);
      return;
    }

    if (commande.payment_mode === "echelonne") {
      if (residual <= 0) {
        setStateButton(true);
        return;
      }
      if (acompteDue) {
        const ok = value === Number(minAmountToPay);
        setStateButton(!ok);
        setErrorMontantAPayer(ok ? "" : `Le montant doit être exactement l'acompte requis (${formatPrice(minAmountToPay)}).`);
        return;
      } else {
        if (value < Number(minAmountToPay)) {
          setErrorMontantAPayer(`Le montant doit être ≥ ${formatPrice(minAmountToPay)}.`);
          setStateButton(true);
          return;
        }
        if (value > residual) {
          setErrorMontantAPayer("Le montant à payer ne doit pas dépasser le montant restant à payer");
          setStateButton(true);
          return;
        }
        setErrorMontantAPayer("");
        setStateButton(false);
        return;
      }
    }

    if (commande.payment_mode === "online") {
      if (value < 1000) {
        setErrorMontantAPayer("Le montant minimum est de 1000 FCFA");
        setStateButton(true);
      } else if (value !== Number(commande.amount_total || 0)) {
        setErrorMontantAPayer("Le montant à payer doit correspondre au total de la commande");
        setStateButton(true);
      } else {
        setErrorMontantAPayer("");
        setStateButton(false);
      }
      return;
    }

    setStateButton(true);
  }, [commande, montantAPayer, acompteDue, minAmountToPay, isTypeOrder]);

  // Affichage du loader pendant le chargement


  // Affichage principal
  return (
    <>
      <SEOHeader
        title="CCBM Shop | Détails commande"
        description="Consultez les détails de votre commande sur CCBM Shop, votre destination pour l'électroménager de qualité."
        keywords="détails commande, CCBM Shop, ccbme, suivi commande, paiement électroménager"
      />
      <Layout childrenClasses="pt-0 pb-0">
        <div className="checkout-page-wrapper w-full bg-white pb-[60px]">
          <div className="w-full mb-5">
            <PageTitle
              title="Détails Commande"
              breadcrumb={[
                { name: "Accueil", path: "/" },
                { name: "Commandes", path: "/profile#order" },
                { name: "Détails Commande" },
              ]}
            />
          </div>
          {isLoading ?
            <>
              <div className="flex justify-center items-center h-screen">
                <Loader2 className="animate-spin mr-2" size={48} />
                <span className="text-lg">Chargement de la commande...</span>
              </div>

            </> :
            (
              <div className="checkout-main-content w-full">
                <div className="container-x mx-auto">
                  {/* Section d'informations de la commande */}
                  <OrderInfoSection commande={commande} />

                  {/* Section des détails de la commande */}
                  <OrderDetailsSection commande={commande} />

                  {/* Section des actions de paiement */}
                  <PaymentActionsSection
                    commande={commande}
                    montantAPayer={montantAPayer}
                    setMontantAPayer={setMontantAPayer}
                    errorMontantAPayer={errorMontantAPayer}
                    stateButton={stateButton}
                    isLoading={isLoading}
                    validerPaiment={validerPaiment}
                    minAmountToPay={minAmountToPay}
                    acompteDue={acompteDue}
                    isTypeOrder={isTypeOrder}
                  />

                  {/* Modal de paiement */}
                  {showPaymentModal && commande && (
                    <WaveOrangePaymentService
                      handlePay={handlePay}
                      totalAmount={montantAPayer}
                      onClose={() => setShowPaymentModal(false)}
                      order={commande}
                      idOrder={commande.id}
                    />
                  )}
                </div>
              </div>
            )}
        </div>
      </Layout>
    </>
  );
}

// Composant pour afficher les informations de la commande
const OrderInfoSection = ({ commande }) => (
  <div className="w-full sm:mb-10 mb-5">
    <div className="sm:flex sm:space-x-[18px]">
      <OrderInfoItem label="N°Commande" value={commande.name} />
      {commande.payment_mode === "domicile" && (
        <OrderInfoItem
          label="Paiement"
          value={commande.advance_payment_status === "not_paid" ? "A domicile" : "Non Payé"}
          className={commande.advance_payment_status === "not_paid" ? "text-red-500" : "text-green-500"}
        />
      )}
      {commande.payment_mode === "online" && (
        <OrderInfoItem
          label="Paiement"
          value={commande.advance_payment_status === "not_paid" ? "Non Payé" : "Payé"}
          className={commande.advance_payment_status === "not_paid" ? "text-red-500" : "text-green-500"}
        />
      )}
      {commande.payment_mode === "echelonne" && (
        <OrderInfoItem
          label="Paiement"
          value={
            commande.advance_payment_status === "not_paid" ? "Non Payé" :
              commande.advance_payment_status === "paid" ? "Payé" : "Partiellement Payé"
          }
          className={
            commande.advance_payment_status === "not_paid" ? "text-red-500" :
              commande.advance_payment_status === "paid" ? "text-green-500" :
                commande.advance_payment_status === "partial" ? "text-yellow-500" : ""
          }
        />
      )}
      <OrderInfoItem
        label="Statut"
        value={
          commande.state === "sale" ? "Validée" :
            commande.state === "to_delivered" ? "En cours de livraison" :
              commande.state === "delivered" ? "Livrée" : "Non validé"
        }
        className={
          commande.state === "sale" ? "text-green-500" :
            commande.state === "to_delivered" ? "text-yellow-500" :
              commande.state === "delivered" ? "text-green-600" : "text-gray-500"
        }
      />
    </div>

    {/* Stats montant si échelonné */}
    {commande.payment_mode === "echelonne" && (
      <div className="sm:flex sm:space-x-[18px] mt-5">
        <OrderInfoItem
          label="Montant Restant"
          value={`${formatPrice(commande.amount_residual)}`}
        />
        <OrderInfoItem
          label="Montant Total"
          value={
            ["not_paid", "paid"].includes(commande.advance_payment_status) ? (
              <span className={`text-${commande.advance_payment_status === "not_paid" ? "red" : "green"}-500`}>
                {formatPrice(commande.amount_total)}
              </span>
            ) : <> {formatPrice(commande.amount_total)}</>
          }
        />
        <OrderInfoItem
          label="Montant Avancé"
          value={formatPrice(commande.amount_total - commande.amount_residual)}
        />
      </div>
    )}

    <div className="sm:flex sm:space-x-[18px] mt-5">
      <OrderInfoItem
        label="Mode de Paiement"
        value={
          commande.payment_mode === "online" ? "En ligne" :
            commande.payment_mode === "domicile" ? "A domicile" : "Echelonné"
        }
      />
      <OrderInfoItem label="Date de livraison" value={formatDate(commande.commitment_date)} />
    </div>
  </div>
);

// Composant pour afficher les détails de la commande
const OrderDetailsSection = ({ commande }) => (
  <div className="w-full max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
    <h1 className="sm:text-2xl text-xl text-qblack font-medium mb-5">Récapitulatif de la commande</h1>
    <div className="w-full p-4 sm:p-6 border border-[#EDEDED] rounded-lg shadow-sm">
      <div className="sub-total mb-6">
        <div className="flex justify-between mb-5">
          <p className="text-[13px] font-medium text-qblack uppercase">Produit</p>
          {commande.type_order === "commande" && <p className="text-[13px] font-medium text-qblack uppercase">Total</p>}
        </div>
      </div>
      <div className="product-list w-full mb-[30px]">
        <ul className="flex flex-col space-y-5">
          {commande.order_lines.map((produit, index) => (
            <li key={index}>
              <div className="flex justify-between items-center">
                <div>
                  <h4 className="text-[15px] text-qblack mb-2.5">
                    {produit.product_name}
                    <sup className="text-[13px] text-qgray ml-2 mt-2">
                      {commande.type_order === "commande" && <span> = {produit.product_uom_qty} x {formatPrice(produit.price_total)}</span>}
                    </sup>
                  </h4>
                  <p className="text-[13px] text-qgray">{produit.description}</p>
                </div>
                <div>
                  {commande.type_order === "commande" && (
                    <span className="text-[15px] text-qblack font-medium">{formatPrice(produit.price_total)}</span>
                  )}
                </div>
              </div>
            </li>
          ))}
        </ul>
      </div>
      <div className="mt-[30px]">
        <div className="flex justify-between mb-5">
          <p className="text-2xl font-medium text-qblack">Total</p>
          <p className="text-2xl font-medium">
            {["not_paid", "paid"].includes(commande.advance_payment_status) && (
              <span className={`text-${commande.advance_payment_status === "not_paid" ? "red" : "green"}-500`}>
                {formatPrice(commande.amount_total)}
              </span>
            )}
          </p>
        </div>
      </div>
    </div>
  </div>
);

// Composant pour les actions de paiement
const PaymentActionsSection = ({
  commande,
  montantAPayer,
  setMontantAPayer,
  errorMontantAPayer,
  stateButton,
  isLoading,
  validerPaiment,
  minAmountToPay,
  acompteDue,
  isTypeOrder
}) => (
  <div className="w-full max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 mt-8">
    {commande.advance_payment_status === "not_paid" || commande.advance_payment_status === "partial" ? (
      <>
        {commande.payment_mode === "domicile" && (
          <div className="flex justify-center items-center mt-2">
            <span className="text-lg font-medium text-red-500 dark:text-white">
              Le paiement se fera à domicile
            </span>
          </div>
        )}

        {(commande.payment_mode === "echelonne" || isTypeOrder) && (
          <div>
            <div className="input-item mb-5">
              <div className="mb-2 inline-block">
                <Label htmlFor="montant" value="Montant à payer" />
              </div>
              <TextInput
                id="montant"
                placeholder={
                  isTypeOrder
                    ? `Montant libre (> 0)`
                    : (acompteDue ? `Acompte requis: ${formatPrice(minAmountToPay)}` : "Minimum 1000 F CFA")
                }
                label="Montant*"
                name="montantAPayer"
                type="number"
                value={montantAPayer}
                onChange={(e) => setMontantAPayer(e.target.value)}
                max={commande.amount_residual}
                min={isTypeOrder ? 1 : (acompteDue ? Number(minAmountToPay) : 0)}
                required
                className="invalid:border-red-500 invalid:text-red-600 focus:invalid:border-red-500 focus:invalid:ring-red-500"
              />
              {errorMontantAPayer && (
                <p className="text-yellow-500">{errorMontantAPayer}</p>
              )}
            </div>
            <Button
              type="submit"
              onClick={(e) => validerPaiment(e, montantAPayer)}
              className="rounded-lg px-5 py-2.5 font-medium w-full hover:bg-red-500 hover:text-white text-xl"
              disabled={stateButton}
            >
              {isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
              Passer à la caisse ({formatPrice(montantAPayer)})
            </Button>
            <p className="mt-2 text-xs text-gray-500 text-center">
              {isTypeOrder ? (
                <>Paiement libre : choisissez le montant que vous souhaitez (strictement &gt; 0), jusqu’à <strong>{formatPrice(commande.amount_residual)}</strong> restant.</>
              ) : acompteDue ? (
                <>Acompte requis : <strong>{formatPrice(minAmountToPay)}</strong>. Vous devrez solder l’acompte avant de pouvoir payer librement.</>
              ) : (
                <>Paiement libre : choisissez le montant que vous souhaitez (minimum <strong>{formatPrice(Math.max(1000, Number(minAmountToPay) || 0))}</strong>), jusqu’à <strong>{formatPrice(commande.amount_residual)}</strong> restant.</>
              )}
            </p>
          </div>
        )}

        {commande.payment_mode === "online" && !isTypeOrder && (
          <Button
            type="submit"
            onClick={(e) => validerPaiment(e, commande.amount_total)}
            className="rounded-lg px-5 py-2.5 font-medium w-full hover:bg-red-500 hover:text-white text-xl"
            disabled={isLoading}
          >
            {isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
            Passer à la caisse ({formatPrice(commande.amount_total)})
          </Button>
        )}
      </>
    ) : (
      <div className="w-full h-[50px] flex justify-center items-center">
        <span className="text-green-500">Le paiement a été effectué avec succès</span>
      </div>
    )}
  </div>
);

// Composant pour afficher un item d'information
const OrderInfoItem = ({ label, value, className = "" }) => (
  <div className="sm:w-1/3 w-full mb-5 h-[70px]">
    <div className="w-full h-full bg-[#F6F6F6] text-qblack flex justify-center items-center">
      <span className="text-[15px] font-medium">
        {label} &nbsp;: <span className={className}>{value}</span>
      </span>
    </div>
  </div>
);
